@import url(https://fonts.googleapis.com/css?family=Delius);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  font-family: 'Delius', cursive;
  width: 100vw;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center; }

/*Edit Component*/
.title {
  margin-bottom: 10px;
  padding-left: 10px; }

#date {
  font-size: 16px;
  width: 100%;
  padding: 10px 10px;
  box-sizing: border-box; }

.main {
  width: 320px;
  margin-top: 10px; }

.option-genre {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  margin-right: 10px; }

.state-group-inputs {
  display: -webkit-flex;
  display: flex;
  margin-left: 10px; }

.option-genre-label, .option-genre-input {
  margin: 0px;
  padding: 0px 10px;
  font-size: 1.5em; }

.message {
  font-size: 16px;
  width: 100%;
  padding: 10px 10px;
  margin-top: 10px;
  box-sizing: border-box; }

.hidden {
  display: none; }

.btn-group {
  margin-top: 30px; }

.link {
  text-decoration: none; }

.btn-group {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center; }

.edit_btn {
  font-size: 18px;
  width: 100%;
  padding: 10px 6px;
  outline: none; }
  .edit_btn:hover {
    background-color: #242424;
    color: white;
    cursor: pointer; }

.save {
  text-transform: uppercase;
  background-color: #00a2ff;
  border: none; }

.discard {
  background-color: white;
  border: none; }

/*Header Component*/
.header {
  width: 100%; }

.header-text {
  text-align: center;
  font-size: 1.5em;
  color: white;
  width: 100%;
  background-color: #00a2ff;
  padding: 4px 6px; }
  .header-text:hover {
    background-color: #242424;
    color: white;
    cursor: pointer; }

.header .link {
  color: white; }

/*List Component*/
.days-list {
  width: 100%;
  list-style: none;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  margin: 0px;
  padding: 0px;
  margin-top: 14px; }

.days-list-item {
  padding: 10px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center; }

.icon {
  font-size: 40px;
  margin-bottom: 10px; }

.happy {
  color: orange; }
  .happy:hover {
    color: #242424;
    cursor: pointer; }

.unhappy {
  color: #a3a1a1; }

